@import url(https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,300;0,400;0,700;0,900;1,300;1,400;1,700;1,900&display=swap);
body {
  margin: 0;
  font-family: 'Playfair Display', serif;
  /* font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif; */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: 'Playfair Display', sans-serif
    /* , source-code-pro, Menlo, Monaco, Consolas, 'Courier New', */
    /* monospace; */
}

* {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: "Lato", sans-serif;
}

#overlay {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
  background-color: white;
  /* display: none; */
}

#overlay img {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: 64px;
}

main {
  padding: 20px;
  max-width: 1268px;
  margin: 0 auto;
  margin-top: 50px;
  display: -webkit-flex;
  display: flex;
}

#table-section {
  width: 60%;
}

a {
  text-decoration: none;
}

form {
  border: 1px solid black;
  width: 230px;
  padding: 6px 10px;
  border-radius: 20px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  margin: 0 auto;
  margin-bottom: 20px;
}

button,
input,
textarea {
  font-family: "Lato", sans-serif;
  outline: unset;
}

#info-wrapper {
  width: 40%;
  padding: 0 0 20px 0;
  text-align: center;
  font-size: 18px;
  padding: 20px;
}

h1 {
  font-weight: 900;
  letter-spacing: 2px;
}

#info-wrapper p {
  font-style: italic;
  color: gray;
}

#info-content {
  /* display: none; */
  margin-top: 10px;
}

#info-wrapper > div {
  margin-bottom: 5px;
}

textarea {
  white-space: normal;
  width: 100%;
  z-index: 2;
  margin: 0;
  border: 2px solid black;
  border-radius: 0;
  color: #444;
  background-color: transparent;
  overflow: auto;
  resize: none;
  font-size: 16px;
  padding: 5px;
  border-radius: 5px;
}

input {
  font-size: 16px;
  border: unset;
  width: 100%;
}

input:focus::-webkit-input-placeholder, textarea:focus::-webkit-input-placeholder {
  color: transparent;
}

input:focus:-ms-input-placeholder, textarea:focus:-ms-input-placeholder {
  color: transparent;
}

input:focus::placeholder,
textarea:focus::placeholder {
  color: transparent;
}

img {
  width: 16px;
  margin-right: 10px;
  margin-left: 5px;
}

#table-wrapper {
  box-shadow: 0 0px 40px 0px rgba(0, 0, 0, 0.15);
}

#table-headers {
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  overflow: hidden;
}

#table-data {
  max-height: 500px;
  overflow-y: overlay;
}

@-moz-document url-prefix() {
  #table-data {
    overflow-y: scroll;
  }
}

#table-data tr {
  cursor: pointer;
}

#table-data tr:nth-child(even) {
  background-color: #f1f1f1;
}

table {
  border-collapse: collapse;
  width: 100%;
  font-size: 14px;
  text-align: center;
  border-radius: 10px;
}

th {
  font-size: 18px;
  color: #fff;
  line-height: 1.4;
  background-color: black;
  padding-top: 18px;
  padding-bottom: 18px;
}

td {
  font-size: 16px;
  color: #808080;
  line-height: 1.4;
  padding-top: 16px;
  padding-bottom: 16px;
}

.column1 {
  width: 50px;
}

.column2 {
  width: 170px;
}

.column3 {
  width: 170px;
}

.column4 {
  width: 170px;
}

.column5 {
  width: 300px;
}

.active {
  background-color: lightseagreen !important;
}

.active td {
  color: black;
}

img{
  width: 60px !important;
  height: 20px !important;
}
/* .prod_row{
    margin-left: 5%;
    margin-right: 5%;
} */

.card{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-flex-direction: column;
            flex-direction: column;
    margin: 2%;
    width: 15%;
}

.card-body{
    width: 100%;
}

.card-para, .card-title{
    display: -webkit-box;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;
  }

.card-title:hover{
    color: black;
}
.images{
    width: 100%;
    /* display: flex; */
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    
}

.image_list{
    margin: 5px;
    width: 15%;
    height: 30%;
    cursor: pointer;
    padding: 2px;
    border-radius: 6px;
}
.active_image{
    border: 2px solid #009688;
    margin: 5px;
    width: 15%;
    height: 30%;
    cursor: pointer;
    padding: 2px;
    border-radius: 6px;
}

#price-tag{
    color: #009688;
}
.moviedata{
    text-align: center;
    display: -webkit-flex;
    display: flex;
    /* flex-direction: column; */
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    /* margin-left: 1%; */
    -webkit-justify-content: center;
            justify-content: center;
}

.movienames{
    /* margin-top: 5px; */
    margin-left: 1%;
    margin-top: 1%;
}


.top{
    text-align: center;
    background-color: aliceblue;
    padding: 0.5%;
    font-family: sans-serif;
}

img{
    width: 280px;
    height: 400px;
}

img:hover{
    cursor: pointer;
    color: rgba(25, 250, 250, 10%);
}

.inputs{
    margin-top: 0.5%;
    text-align: center;
}


button{
    background-color: aqua;
    border-radius: 10%;
    margin-left: 0.2%;
    /* padding: 0.5%; */
}

a{
    color: black;
    text-decoration: none;
}

.Title{
    text-align: center;
    
}

.overlay{
    transition: .5s ease;
    opacity: 0;
    position: relative;
    top: -35%;
    left: 50%;
    height: 70%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    text-align: center;
    background-color: #f9f1f1;
    overflow: hidden;
}

.details{
    color: #101010;
    font-size: 20px;
    /* margin-top: 20%; */
    position: relative;
    -webkit-align-items: center;
            align-items: center;
    opacity: 0;
    visibility: 0;
    /* width: 100%; */
    /* color: white;
    font-size: 16px; */
    text-align: center;
}

.movienames:hover .overlay {
    opacity: 0.8;
    visibility: 1;
    cursor: pointer;
  }

  .movienames:hover .details{
    opacity: 1;
    visibility: 10;
    cursor: pointer;
  }
